import { TextRenameForm } from "./styled";
import { Button } from "../styled";

export const TaskRename = ({
  task,
  changeTaskContent,
  cancelRenameTask,
  setNewTaskContent,
  newTaskContent,
}) => {
  const onFormSubmit = (event) => {
    event.preventDefault();
  };

  return (
    <TextRenameForm hide={!task.rename} onSubmit={onFormSubmit}>
      <input
        value={newTaskContent}
        type="text"
        placeholder=""
        onChange={({ target }) => setNewTaskContent(target.value)}
      />
      <Button
        type="submit"
        title="Zatwierd≈∫ zmianƒô"
        onClick={() => changeTaskContent(task.id)}
      >
        ‚úî
      </Button>
      <Button
        remove
        type="button"
        title="Anuluj zmianƒô"
        onClick={() => cancelRenameTask(task.id)}
      >
        X
      </Button>
    </TextRenameForm>
  );
};
